<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->

            <template v-slot:item.name="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>

            <template v-slot:item.code="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>

             <template v-slot:item.color="{item}">
                <div class="d-flex justify-center">
                    <span>{{ item.color }}</span>
                </div>
            </template>

            <template v-slot:item.amount_using="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount_using) }} kg</span>
                </div>
            </template>

            <template v-slot:item.amount="{item}">
                <div class="d-flex justify-center">
                    <span>{{ formatPrice(item.amount) }} ta</span>
                </div>
            </template>

            <template v-slot:item.amount_sewed="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount_sewed) }} kg</span>
                </div>
            </template>

            <template v-slot:item.waste_amount="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black" :class="item.waste_percentage >= 20 ? 'text-orange' : ''">{{ formatPrice(item.waste_amount) }} kg ({{(item.waste_percentage).toFixed(2)}}%)</span>
                </div>
            </template>

            <template v-slot:item.waste_percentage="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ (item.amount/item.amount_using).toFixed(2) }} %</span>
                </div>
            </template>

            <template v-slot:item.total_price="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.total_price) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>


            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" 
                    @click="(product = item) && (editModal = true)"
                    v-if="$can('update', 'supply_bichuv_tarixi')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>

                    <div class="cursor-pointer mt-1" 
                    @click="(element = item) && (deleteModal = true)"
                    v-if="$can('delete', 'supply_bichuv_tarixi')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template>

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>

        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>







        <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditDialog">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditBichuvHistory" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                        
                        <div class="mt-5">
                            <span class="text-black ml-1">Necha kg olindi</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'amount_using']}"
                             v-slot="{ errors }">
                                <v-text-field outlined class="mt-2" v-model="product.amount_using"
                                :suffix="'kg'" @input="formatPrice2(product.amount_using, 'amount_using')"
                                placeholder=" Misol: 400" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Necha dona bichildi</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                             v-slot="{ errors }">
                                <v-text-field outlined
                                    v-model="product.amount" :error-messages="errors"
                                    @input="formatPrice2(product.amount, 'amount')"
                                    class="mt-2" placeholder="Misol: 30" suffix="dona">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Bichuv massasi</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'amount_sewed']}"
                             v-slot="{ errors }">
                                <v-text-field v-model="product.amount_sewed" 
                                    outlined :error-messages="errors" suffix="kg"
                                    @input="formatPrice2(product.amount_sewed, 'amount_sewed')"
                                    class="mt-2" placeholder="Misol: 380">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Isporeniya</span>

                            <div class="d-flex">
                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'waste_amount']}"
                                v-slot="{ errors }" class="d-flex align-top w-75">
                                    <v-text-field :value="product.waste_amount" outlined class="mt-2" 
                                    readonly suffix="kg" :error-messages="errors"
                                    @input="formatPrice2(product.waste_amount, 'waste_amount')">
                                    </v-text-field>
                                </ValidationProvider>
                                
                                <div class="d-flex align-top w-35">
                                    <v-text-field
                                        v-model="product.waste_percentage" 
                                        min="1" max="100" readonly
                                        outlined suffix="%" class="mt-2 ml-2">
                                    </v-text-field>
                                </div>
                            </div>
                        </div>

                        <div>
                            <span class="text-black ml-1">Umumiy summa</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                             v-slot="{ errors }">
                                <v-text-field :error-messages="errors"
                                    @input="formatPrice2(product.total_price, 'total_price')"
                                    :value="formatPrice2(product.total_price, 'total_price')"
                                    outlined class="mt-2" v-model="product.total_price"
                                    placeholder="Misol: 240,000 so‘m">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditDialog"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal 
        :deleteModal="deleteModal" 
        :user_id="element.id" 
        :actionName="'updateDelete/deleteHistoryDeleteSewed'"
        :modalTitle="'Mahsulot o‘chirilishi'"
        :modalDesc="'Bu mahsulotni o‘chirmoqchimisiz?'"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components: {
        DeleteModal,
        NoData
    },
    data(){
        return {
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            loading: false,
            deleteModal: false,
            editModal: false,
            headers: [
                {text: 'Mahsulot', value: 'name', align: 'start', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Rang kodi', value: 'color', align: 'center', class: 'table-header'},
                {text: 'Ishlatilgan kg', value: 'amount_using', align: 'center', class: 'table-header'},
                {text: 'Olingan dona', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Bichuv massasi', value: 'amount_sewed', align: 'center', class: 'table-header'},
                {text: 'Isporeniya', value: 'waste_amount', align: 'center', class: 'table-header'},
                {text: 'Nisbat', value: 'waste_percentage', align: 'center', class: 'table-header'},
                {text: 'Summa', value: 'total_price', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            element: {},
            product: {
                amount_using: '',
                amount: 0,
                amount_sewed: '',
                waste_amount: '',
                waste_percentage: '',
                total_price: '',
            },
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh()

        if(!this.$can('update', 'supply_bichuv_tarixi') && !this.$can('delete', 'supply_bichuv_tarixi')){
            this.headers.pop();
        }
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.supply_tailoring_history': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_HISTORY', false)
                }
            },
            deep: true,
            immediate: true
        },
        'product.amount_using': {
            async handler(newVal){
                if(newVal){
                    this.calculateEvaporation()
                    this.clearErrors()
                }
            }, deep: true,
        },
        'product.amount_sewed': {
            async handler(newVal){
                if(newVal){
                    this.calculateEvaporation()
                    // if(newVal > this.product.amount_using){
                    //     this.errorMessage = 'Kiritilgan bichuv massasi olingan massadan oshib ketdi!'
                    //     this.errorPlace = 'tailoring_mass';

                    //     this.errorStatus = true;
                    //     this.$toast.error(this.errorMessage);
                    // }else{
                    this.clearErrors()
                    // }
                }
            }, deep: true,
        },
    },
    computed: {
        ...mapState({
            history_sewed: state => state.supply.history_sewed,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        }),

        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.product.price.toString().replace(/,/g, '')
            let amount = this.product.amount.toString().replace(/,/g, '');

            let result = price * amount
            
            return  this.product.total_price = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods: {
        ...mapActions({
            fetchHistorySewed: 'supply/fetchHistorySewed',
            updateHistoryUpdateSewed: 'updateDelete/updateHistoryUpdateSewed'
        }),
        async refresh(){
            this.loading = true
            await this.fetchHistorySewed({page: this.page, search: this.searchValue, date: this.dateFilterValue})
            this.page = this.history_sewed.current_page;
            this.pageCount = Math.ceil(this.history_sewed.total / 10);
            this.items = this.history_sewed.data;
            this.items.forEach((elem, index) => {
                elem.name = elem.product.name;
                elem.code = elem.product.code;
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },





        // ============= CALCULATIONS START =============
        calculateEvaporation(){
            this.product.waste_amount = parseFloat(this.product.amount_using.toString().replace(/,/g, ''))
             - parseFloat(this.product.amount_sewed.toString().replace(/,/g, ''))
                    
            let a = this.product.amount_sewed.toString().replace(/,/g, '') * 100
            this.product.waste_percentage = a / parseFloat(this.product.amount_using.toString().replace(/,/g, ''))
            this.product.waste_percentage =  (100 - this.product.waste_percentage).toFixed(3);
        },
        // ============= CALCULATIONS START =============





        // ============= PRICE_FORMATTER START =============
        formatPrice2(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'amount_using'){
                    this.product.amount_using = currency
                }else if(input == 'amount_sewed'){
                    this.product.amount_sewed = currency
                }else if(input == 'waste_amount'){
                    this.product.waste_amount = currency
                }else if(input == 'total_price'){
                    this.product.total_price = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                amount_using: '',
                amount: 0,
                amount_sewed: '',
                waste_amount: '',
                waste_percentage: '',
                total_price: '',
            },
            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeEditDialog(){
            this.clearInputs();
            this.editModal = false;
            this.$refs.formEditBichuvHistory.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============














        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formEditBichuvHistory.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============


                    // ============= EDITING_IP START =============
                    let abc = await this.updateHistoryUpdateSewed({ 
                        id: this.product.id,
                        amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                        amount_using: parseFloat(this.product.amount_using.toString().replace(/,/g, '')),
                        amount_sewed: parseFloat(this.product.amount_sewed.toString().replace(/,/g, '')),
                        waste_percentage: parseFloat(this.product.waste_percentage.toString().replace(/,/g, '')),
                        waste_amount: parseFloat(this.product.waste_amount.toString().replace(/,/g, '')),
                        total_price: parseFloat(this.product.total_price.toString().replace(/,/g, '')),
                    })

                    if(abc.response && abc.response.status == 422){
                        if(abc.response.data.errors.amount){
                            this.errorMessage = abc.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(abc.response.data.errors.amount_using){
                            this.errorMessage = abc.response.data.errors.amount_using[0];
                            this.errorPlace = 'amount_using'
                        }else if(abc.response.data.errors.amount_sewed){
                            this.errorMessage = abc.response.data.errors.amount_sewed[0];
                            this.errorPlace = 'amount_sewed'
                        }else if(abc.response.data.errors.waste_amount){
                            this.errorMessage = abc.response.data.errors.waste_amount[0];
                            this.errorPlace = 'waste_amount'
                        }else if(abc.response.data.errors.total_price){
                            this.errorMessage = abc.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(abc.response && abc.response.status == 409){
                        if(abc.response.data.message){
                            this.errorMessage = abc.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli tahrirlandi!'
                        this.$toast.success(toastTitle);
                        this.closeEditDialog();
                        this.refresh();
                        // this.$store.commit('main/SET_UPDATE_MAVJUD_IPS', true)
                    }
                    // ============= EDITING_IP END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        }
        // ============= ON_SUBMIT END =============



        // history-update-sewed
    }
}
</script>

<style>

</style>