            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditIncomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <!-- <div class="mt-4">
                            <span class="text-black ml-1">Turni tanlang</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'code']}"
                              v-slot="{ errors }">
                                <v-select :items="filteredItems" v-model="product.product_category_id" 
                                    class="mt-2" item-value="id" label="Tanlang"
                                    outlined :error-messages="errors" item-text="name">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Nomni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.name" class="mt-2"
                                    placeholder="Misol: Vilvit" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Kodni kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.code" type="number" class="mt-2"
                                    placeholder="Misol: 666777" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Tavsif kiriting</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'description']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="product.description" class="mt-2"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div> -->

                            
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components:{
        DeleteModal,
        NoData
    },
    data(){
        return {
            menu: [],
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Kimga', value: 'company', align: 'start', class: 'table-header'},
                {text: 'Buyurtma mahsulot nomi', value: 'title', align: 'center', class: 'table-header'},
                {text: 'Mahsulot kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Adadi', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Berilgan resurs', value: 'resource', align: 'center', class: 'table-header'},
                {text: 'Dona narxi', value: 'piece_price', align: 'center', class: 'table-header'},
                {text: 'Summa', value: 'sum', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            filteredMato: [],
            filteredYoqa: [],
            filteredAksessuar: []
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.supply_tailoring_order_history': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_ORDER_HISTORY', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            sewing_process_history: state => state.supply.sewing_process_history,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        })
    },
    methods: {
        ...mapActions({
            fetchSewingProcessHistory: 'supply/fetchSewingProcessHistory'
        }),
        async refresh(){
            this.loading = true
            await this.fetchSewingProcessHistory({page: this.page, search: this.searchValue, date: this.dateFilterValue})
            this.page = this.sewing_process_history.current_page;
            this.pageCount = Math.ceil(this.sewing_process_history.total / 10);
            this.items = this.sewing_process_history.data;
            this.items.forEach((elem, index) => {
                elem.num = index
                elem.company = elem.factory.name;
                elem.title = elem.product.name;

                elem.code = elem.product.code;
                elem.piece_price = elem.order.price;
                elem.sum = elem.order.total_price;

                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false

            this.sewing_process_history.data.forEach((item, index) => {
                this.filteredMato[index] = this.sewing_process_history.data[index].order.order_extra_product.filter((elem) => {
                    return elem.product.product_category.parent_id == 2
                })
                this.filteredYoqa[index] = this.sewing_process_history.data[index].order.order_extra_product.filter((elem) => {
                    return elem.product.product_category.parent_id == 3
                })
                this.filteredAksessuar[index] = this.sewing_process_history.data[index].order.order_extra_product.filter((elem) => {
                    return elem.product.product_category.parent_id == 4
                })
            })
            // if(this.sewing_process_history.data[0].order_extra_product){
            //     this.filteredMato = this.sewing_process_history.data[0].order_extra_product.filter((elem) => {
            //         return elem.product.product_category.parent_id == 2
            //     })
            //     this.filteredYoqa = this.sewing_process_history.data[0].order_extra_product.filter((elem) => {
            //         return elem.product.product_category.parent_id == 3
            //     })
            //     this.filteredAksessuar = this.sewing_process_history.data[0].order_extra_product.filter((elem) => {
            //         return elem.product.product_category.parent_id == 4
            //     })
            // }
        },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

