<template>
  <div>
       <v-dialog
            max-width="400"
            persistent
            v-model="productTypeSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma mahsulot turini tanlash</span>
                        <v-btn icon @click="closeProductDialog()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderProductTypeSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit5)">

                            <div class="mt-3"></div>
                            <perfect-scrollbar class="mt-8 mr-1">
                                <div class="radioContent mb-5" v-for="(element, index) in typesList" :key="index">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.typeSelected">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeProductDialog()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="productSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma mahsulotini tanlash</span>
                        <v-btn icon @click="closeProductAddDialog()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderProductSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit6)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="productSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element, index2) in productsList" :key="index2">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider 
                                        :rules="order == '' ? 'required' : ''"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.productSelected">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>


                            <div class="mt-6">
                                <span class="text-black ml-1">Yangi mahsulot qo’shish</span>
                                <ValidationProvider 
                                    :rules="{required: productSelected == '' ? true : false,
                                    error: [errorStatus, errorMessage, errorPlace, 'name']}"
                                    v-slot="{ errors }">

                                    <v-text-field
                                        outlined v-model="order.name" 
                                        label="Nomni kiriting"
                                        class="mt-4" :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>

                                <ValidationProvider :rules="{required: productSelected == '' ? true : false, 
                                error: [errorStatus, errorMessage, errorPlace, 'code']}"
                                    v-slot="{ errors }">
                                    
                                    <v-text-field outlined v-model="order.code"
                                        :counter="8" label="Kodni kiriting"
                                        :error-messages="errors">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'

export default {
    props: ['productTypeSelectDialog', 'productSelectDialog', 'productSelected', 'typeSelected', 'typesList', 
    'productsList'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            productSearch: '',

            order: {
                name: '',
                code: '',
            },

            pageProduct: 1,
            last_pageProduct: 2,
            pageCountProduct: 0,
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){

    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        'order.name': {
            handler(newValue){
                if(newValue != ''){
                    this.clearErrors()
                    this.$parent.productSelected = ''
                }
            },deep: true
        },
        'order.code': {
            handler(newValue){
                if(newValue != ''){
                    this.clearErrors()
                    this.$parent.productSelected = ''
                }
            },deep: true
        },
        productSelected(newValue, oldValue){
            if(newValue != ''){
                this.clearInputs()
            }
        },
        // ============= OTHER_WATCHERS START =============



        // ============= OTHER_WATCHERS START =============
        async typeSelected(newValue, oldValue){
            if(newValue){
                await this.refresh();
            }
        },


        async productSearch(newValue, oldValue){
            this.pageProduct = 1;
            if(newValue || newValue == ''){
                await this.refresh();
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState(
            {
                productCodesFields: state => state.code.productCodesFields,
            }
        ),
    },
    methods: {
        ...mapActions(
            {
                saveProductCode: 'code/saveProductCode',
                fetchProductCodes: 'code/fetchProductCodes',
            }
        ),



         // ============= REFRESH START =============
        async refresh(){
            this.pageProduct = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchProductCodes({page: this.pageProduct, status: this.$parent.typeSelected.id, search: this.productSearch});

                this.pageProduct = this.productCodesFields.current_page;
                this.last_pageProduct = this.productCodesFields.last_page;
                this.pageCountProduct = Math.ceil(this.productCodesFields.total / 20);
                this.$parent.productsList = this.productCodesFields.data;
            }, 500)
        },
        // ============= REFRESH START =============



        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.pageProduct < this.last_pageProduct){
                this.pageProduct = this.pageProduct + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchProductCodes({page: this.pageProduct, status: this.$parent.typeSelected.id, search: this.productSearch});

                    this.pageProduct = this.productCodesFields.current_page;
                    this.pageCountProduct = Math.ceil(this.productCodesFields.total / 20);

                    this.$parent.productsList = this.$parent.productsList.concat(this.productCodesFields.data);
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============




        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.clearErrors();
            this.order.name = '';
            this.order.code = '';
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeProductAddDialog(){
            this.clearInputs();
            this.$parent.productSelectDialog = false
        },
        closeProductDialog(){
            this.$parent.productTypeSelectDialog = false;
        },
        // ============= CLEARS_AND_CLOSES END =============







        // ============= ON_SUBMIT5 START =============
        async onSubmit5(){
            this.$refs.formOrderProductTypeSelect.validate().then(async(success) => {
                if(success){
                    this.closeProductDialog()
                }
            })
        },
        // ============= ON_SUBMIT5 END =============



        
        // ============= ON_SUBMIT6 START =============
        async onSubmit6(){
            this.$refs.formOrderProductSelect.validate().then(async(success) => {
                if(success){
                    if(this.order.name != '' && this.order.code != ''){
                        let a = await this.saveProductCode({
                            code: this.order.code,
                            product_category_id: parseInt(this.typeSelected.id),
                            name: this.order.name,
                        })
                        this.$parent.productSelected = a;

                        if (a.response && a.response.status == 422) {
                            if(a.response.data.errors.code){
                                this.errorMessage = a.response.data.errors.code[0];
                                this.errorPlace = 'code';
                            }else if(a.response.data.errors.name){
                                this.errorMessage = a.response.data.errors.name[0];
                                this.errorPlace = 'name';
                            }
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }else if(a.response && a.response.status == 409){
                            if(a.response.data.message){
                                this.errorMessage = a.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            this.$toast.success('Muvaffaqiyatli qo`shildi!');
                            this.closeProductAddDialog();
                        }
                    }else{
                        this.closeProductAddDialog();
                    }
                }
            })
        },
        // ============= ON_SUBMIT6 END =============


       
    }

}
</script>

<style>

</style>