<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="aksessuarTypeSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma aksessuar turini tanlash</span>
                        <v-btn icon @click="closeAksessuarTypeSelect()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderAccessoryTypeSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit12)">
                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="aksessuarTypeSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1">
                                <div class="radioContent mb-5" v-for="(element, index) in aksessuarTypesList" :key="index">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.aksessuarList[aksessuarIndex].aksessuarType">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeAksessuarTypeSelect()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="aksessuarSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma aksessuarni tanlash</span>
                        <v-btn icon @click="closeAksessuarSelect()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderAccessorySelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit13)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="aksessuarSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>
                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element, index2) in aksessuars" :key="index2">
                                    <span>{{element.product.name}}</span>
                                    
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }" class="d-flex align-center">
                                        <span class="mr-4 text-blue">{{formatPrice(element.amount)}} dona</span>
                                        <v-radio-group class="radioGroup mr-1" :error-messages="errors"
                                            v-model="$parent.aksessuarList[aksessuarIndex].aksessuar">
                                            <v-radio :value="element.product"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeAksessuarSelect()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { priceMixin } from '@/mixins/formatter.js'

export default {
    props: ['aksessuarTypeSelectDialog', 'aksessuarSelectDialog', 'aksessuarList', 'aksessuarIndex'],
    mixins: [dateMixin, loaderMixin, priceMixin],
    components: {
        PerfectScrollbar
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            pageAksessuar: 1,
            last_pageAksessuar: 2,
            pageCountAksessuar: 0,

            aksessuarTypesList: [],

            aksessuarTypeSearch: '',
            aksessuarSearch: '',

            aksessuars: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh()
        await this.refresh2()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        async aksessuarTypeSearch(newValue, oldValue){
            if(newValue){
                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    if(newValue){
                        await this.fetchCategories({search: newValue});
                    }else{
                        await this.fetchCategories();
                    }
                    this.aksessuarTypesList = this.productCategories.filter((elem) => elem.parent_id == 4);
                }, 500)
            }
        },


        async aksessuarSearch(newValue, oldValue){
            this.pageAksessuar = 1;
            if(newValue || newValue == ''){
                await this.refresh2();
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState(
            {
                productCategories: state => state.categories.productCategories,
                exist_products: state => state.warehouse.exist_products
            }
        ),
    },
    methods: {
        ...mapActions(
            {
                fetchCategories: 'categories/fetchCategories',
                fetchExistProducts: 'warehouse/fetchExistProducts'
            }
        ),
        // ============= REFRESH START =============
        async refresh(){
            await this.fetchCategories()
            this.aksessuarTypesList = this.productCategories.filter((elem) => elem.parent_id == 4)
        },
        // ============= REFRESH START =============





        // ============= REFRESH START =============
        async refresh2(){
            this.pageAksessuar = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchExistProducts({page: this.pageAksessuar, storage_type: 2, status: 4, 
                category_id: this.$parent.aksessuarList[this.aksessuarIndex].aksessuarType.id,
                search: this.aksessuarSearch});

                this.pageAksessuar = this.exist_products.current_page;
                this.last_pageAksessuar = this.exist_products.last_pageAksessuar;
                this.pageCountAksessuar = Math.ceil(this.exist_products.total / 10);
                
                this.aksessuars = this.exist_products.data;
            }, 500)
        },
        // ============= REFRESH END =============







        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.pageAksessuar < this.last_pageAksessuar){
                this.pageAksessuar = this.pageAksessuar + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchExistProducts({page: this.pageAksessuar, storage_type: 2, 
                    status: 4, category_id: this.$parent.aksessuarList[this.aksessuarIndex].aksessuarType.id});

                    this.pageAksessuar = this.exist_products.current_page;
                    this.pageCountAksessuar = Math.ceil(this.exist_products.total / 10);
                    
                    this.aksessuars = this.exist_products.data;
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============




        // ============= CLEARS_AND_CLOSES START =============
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeAksessuarTypeSelect(){
            this.clearErrors()
            this.$refs.formOrderAccessoryTypeSelect.reset();
            this.$parent.aksessuarTypeSelectDialog = false
        },
        closeAksessuarSelect(){
            this.clearErrors()
            this.$refs.formOrderAccessorySelect.reset();
            this.$parent.aksessuarSelectDialog = false;
        },
        // ============= CLEARS_AND_CLOSES END =============




        


        // ============= ON_SUBMIT12 START =============
        async onSubmit12(){
            this.$refs.formOrderAccessoryTypeSelect.validate().then(async(success) => {
                if(success){
                    this.closeAksessuarTypeSelect()

                    await this.refresh2()
                }
            })
        },
        // ============= ON_SUBMIT12 END =============



        // ============= ON_SUBMIT13 START =============
        async onSubmit13(){
            this.$refs.formOrderAccessorySelect.validate().then(async(success) => {
                if(success){
                    this.closeAksessuarSelect()
                }
            })
        },
        // ============= ON_SUBMIT13 END =============

    }

}
</script>

