<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="readyProduct"
        >
            <template v-slot:activator="{ on, attrs }">

                <v-btn dark v-bind="attrs"
                       v-on="on" elevation="0" class="mr-2" style="background: #2886FF;">
                    <img src="@/assets/icons/export.svg" alt="export" class="mr-1" width="24">
                    <span class="fs-16px" style="text-transform: none;">Buyurtma</span>
                </v-btn>

            </template>


            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black ml-1"> Buyurtma berilmoqda</span>
                    <v-btn icon @click="readyProduct = false">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <div class="aksessuar_add__content">
                    <v-stepper v-model="e1" elevation="0">

                        <v-stepper-header class="elevation-0">
                            <v-stepper-step :complete="e1 > 1" step="1"></v-stepper-step>

                            <v-divider></v-divider>

                            <v-stepper-step :complete="e1 > 2" step="2"></v-stepper-step>

                            <v-divider></v-divider>

                            <v-stepper-step :complete="e1 > 3" step="3"></v-stepper-step>

                            <v-divider></v-divider>

                            <v-stepper-step step="4"></v-stepper-step>
                        </v-stepper-header>



                        <v-stepper-items>
                            <v-stepper-content step="1">

                                <ValidationObserver ref="formSupplyOrder1" v-slot="{ handleSubmit }">
                                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit1)">

                                        <div class="mt-3">
                                            <span class="text-black ml-1">Sana</span>

                                            <v-menu
                                                v-model="dateMenu"
                                                :close-on-content-click="false"
                                                :nudge-right="40"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="auto"
                                            >
                                                <template v-slot:activator="{ on, attrs }">
                                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                                        <v-text-field
                                                            class="mt-2"
                                                            placeholder="Tanlang"
                                                            v-model="dateFormatted"
                                                            readonly
                                                            outlined
                                                            v-bind="attrs"
                                                            v-on="on"
                                                            @blur="date = parseDate(dateFormatted)"
                                                            :error-messages="errors"
                                                        ></v-text-field>
                                                    </ValidationProvider>
                                                </template>
                                                <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-date-picker
                                                    color="blue lighten-1"
                                                    v-model="date"
                                                    @input="dateMenu = false"
                                                    locale="ru-ru"
                                                    first-day-of-week="1"
                                                    :error-messages="errors"
                                                ></v-date-picker>
                                                </ValidationProvider>
                                            </v-menu>
                                        </div>

                                        <span class="text-black ml-1">Qaysi fabrikaga</span>
                                        <ValidationProvider :rules="{required: true}"
                                            v-slot="{ errors }">
                                            <v-select :items="factoriesList" item-text="name"
                                                :error-messages="errors" item-value="id" v-model="factorySelected"
                                                label="Tanlang" outlined class="mt-2" >
                                            </v-select>
                                        </ValidationProvider>


                                        <span class="text-black ml-1">Buyurtma mahsuloti turi</span>
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field append-icon="mdi-chevron-right"
                                                @click="productTypeSelectDialog = true"
                                                label="Tanlang" outlined class="mt-5"
                                                :value="typeSelected.name"
                                                :error-messages="errors">
                                            </v-text-field>
                                        </ValidationProvider>


                                        <span class="text-black ml-1">Buyurtma mahsuloti nomi</span>
                                        <ValidationProvider :rules="{required: true,
                                         error: [errorStatus, errorMessage, errorPlace, 'name']}"
                                         v-slot="{ errors }">
                                            <v-text-field append-icon="mdi-chevron-right"
                                                @click="productSelectDialog = true"
                                                outlined :value="productSelected.name"
                                                label="Tanlang" :disabled="!typeSelected"
                                                class="mt-2" :error-messages="errors">
                                            </v-text-field>
                                        </ValidationProvider>


                                        <span class="text-black ml-1">Buyurtma adadi</span>
                                        <ValidationProvider :rules="{required: true,
                                        error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                                         v-slot="{ errors }">
                                            <v-text-field outlined v-model="orderAmount"
                                                @input="formatPrice(orderAmount, 'amount')"
                                                onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                label="Misol: 1,500 ta"
                                                class="mt-2" :error-messages="errors">
                                            </v-text-field>
                                        </ValidationProvider>


                                        <v-card-actions class="d-flex justify-space-between">
                                            <v-btn
                                                text
                                                class="backBtn"
                                                elevation="0"
                                                @click="readyProduct = false"
                                            >Bekor qilish
                                            </v-btn>
                                            <v-btn
                                                class="primaryBtn"
                                                elevation="0"
                                                type="submit"
                                                @click="e1 = 2"
                                            >Davom etish
                                                <!-- :disabled="invalid" -->
                                            </v-btn>
                                        </v-card-actions>

                                    </form>
                                </ValidationObserver>

                            </v-stepper-content>



                            <v-stepper-content step="2">
                                <ValidationObserver ref="formSupplyOrder2" v-slot="{ handleSubmit }">
                                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit2)">

                                        <div v-for="(item, index1) in matosList" :key="index1">
                                            <div class="border-top border-bottom border-left border-right pa-2
                                                d-flex align-center justify-space-between mb-5" v-if="index1 != 0">
                                                <span class="ml-2">{{index1 + 1}} - mato ma'lumotlari</span>
                                                <v-btn icon @click="removeMatoInput(index1)">
                                                    <img src="@/assets/images/BackButton.svg" alt="BackButton">
                                                </v-btn>
                                            </div>

                                            <span class="text-black ml-1">Mato turini tanlang</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field append-icon="mdi-chevron-right"
                                                    @click="openMatoTypeSelect(index1)"
                                                    label="Tanlang" outlined class="mt-5"
                                                    :value="item.matoType.name"
                                                    :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>



                                            <span class="text-black ml-1">Matoni tanlang</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field append-icon="mdi-chevron-right"
                                                    @click="matoSelectDialog = true"
                                                    label="Tanlang" outlined class="mt-5"
                                                    :value="item.mato.product ? item.mato.product.name : ''"
                                                    :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>



                                            <span class="text-black ml-1">Adadi</span>
                                            <ValidationProvider :rules="{required: true,
                                            error: [errorStatus, errorMessage, errorPlace, 'mato_adadi']}"
                                            v-slot="{ errors }">
                                                <v-text-field outlined v-model="item.amount"
                                                    @input="formatPrice(item.amount, 'mato_adadi', index1)"
                                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                    label="Misol: 1,500 ta"
                                                    class="mt-2" :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>
                                        </div>


                                        <button class="btn-new-white mb-5" type="button" @click="addMatoInputs()">
                                            <img src="@/assets/icons/add-circle-blue.svg"
                                            class="mr-1" alt="add-blue" style="width: 20px;">
                                            <span class="text-blue">Qo`shimcha qo`shish</span>
                                        </button>


                                        <div v-for="(item, index2) in yoqasList" :key="index2">
                                            <div class="border-top border-bottom border-left border-right pa-2
                                                d-flex align-center justify-space-between mb-5" v-if="index2 != 0">
                                                <span class="ml-2">{{index2 + 1}} - yoqa ma'lumotlari</span>
                                                <v-btn icon @click="removeYoqaInput(index2)">
                                                    <img src="@/assets/images/BackButton.svg" alt="BackButton">
                                                </v-btn>
                                            </div>

                                            <span class="text-black ml-1">Yoqa turini tanlang</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field append-icon="mdi-chevron-right"
                                                    @click="openYoqaTypeSelect(index2)"
                                                    label="Tanlang" outlined class="mt-5"
                                                    :value="item.yoqaType.name"
                                                    :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>



                                            <span class="text-black ml-1">Yoqani tanlang</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field append-icon="mdi-chevron-right"
                                                    @click="yoqaSelectDialog = true"
                                                    label="Tanlang" outlined class="mt-5"
                                                    :value="item.yoqa.product ? item.yoqa.product.name : ''"
                                                    :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>


                                            <span class="text-black ml-1">Adadi</span>
                                            <ValidationProvider :rules="{required: true,
                                             error: [errorStatus, errorMessage, errorPlace, 'yoqa_adadi']}"
                                             v-slot="{ errors }">
                                                <v-text-field outlined v-model="item.amount"
                                                    @input="formatPrice(item.amount, 'yoqa_adadi', index2)"
                                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                    label="Misol: 1,500 ta"
                                                    class="mt-2" :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>

                                        </div>


                                        <button class="btn-new-white mb-5" type="button" @click="addYoqaInputs()">
                                            <img src="@/assets/icons/add-circle-blue.svg"
                                            class="mr-1" alt="add-blue" style="width: 20px;">
                                            <span class="text-blue">Qo`shimcha qo`shish</span>
                                        </button>


                                        <v-card-actions class="d-flex justify-space-between">
                                            <v-btn
                                                text
                                                class="backBtn"
                                                elevation="0"
                                                @click="e1 = 1"
                                            >Ortga qaytish
                                            </v-btn>
                                            <v-btn
                                                class="primaryBtn"
                                                elevation="0"
                                                type="submit"
                                                @click="e1 = 3"
                                            >Davom etish
                                            </v-btn>
                                        </v-card-actions>
                                    </form>
                                </ValidationObserver>

                            </v-stepper-content>



                            <v-stepper-content step="3">
                                <ValidationObserver ref="formSupplyOrder3" v-slot="{ handleSubmit }">
                                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit3)">

                                        <div v-for="(item, index3) in aksessuarList" :key="index3">
                                            <div class="border-top border-bottom border-left border-right pa-2
                                                d-flex align-center justify-space-between mb-5" v-if="index3 != 0">
                                                <span class="ml-2">{{index3 + 1}} - aksessuar ma'lumotlari</span>
                                                <v-btn icon @click="removeAksessuarInput(index3)">
                                                    <img src="@/assets/images/BackButton.svg" alt="BackButton">
                                                </v-btn>
                                            </div>

                                            <span class="text-black ml-1">Aksessuar turini tanlang</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field append-icon="mdi-chevron-right"
                                                    @click="openAksessuarTypeSelect(index3)"
                                                    label="Tanlang" outlined class="mt-5"
                                                    :value="item.aksessuarType.name"
                                                    :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>



                                            <span class="text-black ml-1">Aksessuarni tanlang</span>
                                            <ValidationProvider rules="required" v-slot="{ errors }">
                                                <v-text-field append-icon="mdi-chevron-right"
                                                    @click="aksessuarSelectDialog = true"
                                                    label="Tanlang" outlined class="mt-5"
                                                    :value="item.aksessuar.name"
                                                    :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>


                                            <span class="text-black ml-1">Adadi</span>
                                            <ValidationProvider :rules="{required: true,
                                             error: [errorStatus, errorMessage, errorPlace, 'aksessuar_adadi']}"
                                              v-slot="{ errors }">
                                                <v-text-field outlined v-model="item.amount"
                                                    @input="formatPrice(item.amount, 'aksessuar_adadi', index3)"
                                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                    label="Misol: 1,500 ta"
                                                    class="mt-2" :error-messages="errors">
                                                </v-text-field>
                                            </ValidationProvider>

                                        </div>

                                        <button class="btn-new-white mb-5" type="button" @click="addAksessuarInputs()">
                                            <img src="@/assets/icons/add-circle-blue.svg"
                                            class="mr-1" alt="add-blue" style="width: 20px;">
                                            <span class="text-blue">Qo`shimcha qo`shish</span>
                                        </button>

                                        <v-card-actions class="d-flex justify-space-between">
                                            <v-btn
                                                text
                                                class="backBtn"
                                                elevation="0"
                                                @click="e1 = 2"
                                            >Ortga qaytish
                                            </v-btn>
                                            <v-btn
                                                class="primaryBtn"
                                                elevation="0"
                                                type="submit"
                                                @click="e1 = 4"
                                            >Davom etish
                                            </v-btn>
                                        </v-card-actions>
                                    </form>
                                </ValidationObserver>

                            </v-stepper-content>


                            <v-stepper-content step="4">
                                <ValidationObserver ref="formSupplyOrder4" v-slot="{ handleSubmit, invalid }">
                                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit4)">

                                        <span class="text-black ml-1">Buyurtma dona narxi</span>
                                        <ValidationProvider :rules="{required: true,
                                         error: [errorStatus, errorMessage, errorPlace, 'price']}"
                                          v-slot="{ errors }">
                                            <v-text-field outlined v-model="orderPrice"
                                                @input="formatPrice(orderPrice, 'price')"
                                                onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                label="Misol: 20,000 so`m"
                                                class="mt-2" :error-messages="errors">
                                            </v-text-field>
                                        </ValidationProvider>


                                        <span class="text-black ml-1">Buyurtma umumiy narxi</span>
                                        <ValidationProvider :rules="{required: true,
                                         error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                                          v-slot="{ errors }">
                                            <v-text-field outlined v-model="orderTotalPrice"
                                                @input="formatPrice(orderTotalPrice, 'total_price')"
                                                onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                :value="getTotalPrice"
                                                label="Misol: 500,000 so`m"
                                                class="mt-2" :error-messages="errors">
                                            </v-text-field>
                                        </ValidationProvider>


                                        <span class="text-black ml-1">Pul berildi</span>
                                        <ValidationProvider 
                                        :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}"
                                          v-slot="{ errors }">
                                            <v-text-field outlined v-model="orderPaid"
                                                @input="formatPrice(orderPaid, 'paid')"
                                                onkeypress="return /[0-9,/.]/i.test(event.key)"
                                                label="Misol: 500,000 so`m"
                                                class="mt-2" :error-messages="errors">
                                            </v-text-field>
                                        </ValidationProvider>

                                        <v-card-actions class="d-flex justify-space-between">
                                            <v-btn
                                                text
                                                class="backBtn"
                                                elevation="0"
                                                @click="e1 = 3"
                                            >Ortga qaytish
                                            </v-btn>
                                            <v-btn
                                                class="primaryBtn"
                                                elevation="0"
                                                :loading="loading"
                                                :disabled="invalid"
                                                type="submit"
                                                @click="loader = 'loading'"
                                            >Tasdiqlash
                                            </v-btn>
                                        </v-card-actions>
                                    </form>
                                </ValidationObserver>

                            </v-stepper-content>

                        </v-stepper-items>
                    </v-stepper>
                </div>


            </v-card>
        </v-dialog>



        <ProductDialog :productTypeSelectDialog="productTypeSelectDialog" :productSelectDialog="productSelectDialog"
        :productSelected="productSelected" :typeSelected="typeSelected" :typesList="typesList"
        :productsList="productsList"></ProductDialog>

        <MaterialDialog :matoTypeSelectDialog="matoTypeSelectDialog" :matoSelectDialog="matoSelectDialog"
        :matosList="matosList" :matoIndex="matoIndex"></MaterialDialog>

        <CollarDialog :yoqaTypeSelectDialog="yoqaTypeSelectDialog" :yoqaSelectDialog="yoqaSelectDialog"
        :yoqasList="yoqasList" :yoqaIndex="yoqaIndex"></CollarDialog>

        <AccessoryDialog :aksessuarTypeSelectDialog="aksessuarTypeSelectDialog"
        :aksessuarSelectDialog="aksessuarSelectDialog"
        :aksessuarList="aksessuarList" :aksessuarIndex="aksessuarIndex"></AccessoryDialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'
import ProductDialog from './modals/ProductDialog.vue'
import MaterialDialog from './modals/MaterialDialog.vue'
import CollarDialog from './modals/CollarDialog.vue'
import AccessoryDialog from './modals/AccessoryDialog.vue'

export default {
    mixins: [dateMixin, loaderMixin],
    components: {
        ProductDialog,
        MaterialDialog,
        CollarDialog,
        AccessoryDialog
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            e1: 1,
            readyProduct: false,

            //-------------Dialogs---------------
            productTypeSelectDialog: false,
            productSelectDialog: false,

            matoTypeSelectDialog: false,
            matoSelectDialog: false,

            yoqaTypeSelectDialog: false,
            yoqaSelectDialog: false,

            aksessuarTypeSelectDialog: false,
            aksessuarSelectDialog: false,
            //-------------Dialogs---------------


            //-------------Lists---------------
            factoriesList: [],
            factorySelected: '',

            typesList: [],
            typeSelected: '',

            productsList: [],
            productSelected: '',
            //-------------Lists---------------

            orderAmount: '',
            orderPrice: '',
            orderTotalPrice: '',
            orderPaid: '',


            matosList: [{matoType: {}, mato: {}, amount: 0}],
            yoqasList: [{yoqaType: {}, yoqa: {}, amount: 0}],
            aksessuarList: [{aksessuarType: {}, aksessuar: {}, amount: 0}],

            matoIndex: '',
            yoqaIndex: '',
            aksessuarIndex: '',

            page: 1,
            pageCount: 0,
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        orderAmount(newValue, oldValue){
            if(newValue){
                this.clearErrors()
            }
        },
        orderPrice(newValue, oldValue){
            if(newValue){
                this.clearErrors()
            }
        },
        orderTotalPrice(newValue, oldValue){
            if(newValue){
                this.clearErrors()
            }
        },
        orderPaid(newValue, oldValue){
            if(newValue){
                this.clearErrors()
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState(
            {
                productCategories: state => state.categories.productCategories,
                productCodesFields: state => state.code.productCodesFields,
                factories: state => state.categories.factories,
            }
        ),
        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.orderPrice.toString().replace(/,/g, '')
            let amount = this.orderAmount.toString().replace(/,/g, '');

            let result = price * amount

            return  this.orderTotalPrice = new Intl.NumberFormat('en-US').format(result)
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
    },
    methods: {
        ...mapActions(
            {
                fetchCategories: 'categories/fetchCategories',
                fetchFactories: 'categories/fetchFactories',
                postTailoringOrder: 'supply/postTailoringOrder',
            }
        ),





        // ============= REFRESH START =============
        async refresh(){
            // this.factorySelected = ''
            await this.fetchFactories();
            this.factoriesList = this.factories.filter((e) => e.factory_category.type == 'factory')


            await this.fetchCategories()
            this.typesList = this.productCategories.filter((elem) => elem.parent_id == 5)
        },
        // ============= REFRESH END =============





        // ============= OPEN_TYPE_SELECTS START =============
        openMatoTypeSelect(index){
            this.matoIndex = index
            this.matoTypeSelectDialog = true
        },
        openYoqaTypeSelect(index){
            this.yoqaIndex = index
            this.yoqaTypeSelectDialog = true
        },
        openAksessuarTypeSelect(index){
            this.aksessuarIndex = index
            this.aksessuarTypeSelectDialog = true
        },
        // ============= OPEN_TYPE_SELECTS END =============




        // ============= ADD_INPUTS START =============
        addMatoInputs(){
            if(this.matosList.length < 10){
                this.matosList.push({matoType: {}, mato: {}, amount: 0})
            }else{
                this.$toast.error('Beshtadan ko`p mato qo`shib bo`lmaydi')
            }
        },
        addYoqaInputs(){
            if(this.yoqasList.length < 10){
                this.yoqasList.push({yoqaType: {}, yoqa: {}, amount: 0})
            }else{
                this.$toast.error('Beshtadan ko`p yoqa qo`shib bo`lmaydi')
            }
        },
        addAksessuarInputs(){
            if(this.aksessuarList.length < 20){
                this.aksessuarList.push({aksessuarType: {}, aksessuar: {}, amount: 0})
            }else{
                this.$toast.error('Beshtadan ko`p aksessuar qo`shib bo`lmaydi')
            }
        },
        removeMatoInput(index){
            this.matosList.splice(index, 1);
        },
        removeYoqaInput(index){
            this.yoqasList.splice(index, 1);
        },
        removeAksessuarInput(index){
            this.aksessuarList.splice(index, 1);
        },
        // ============= ADD_INPUTS END =============






        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input, index) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.orderAmount = currency;
                }else if(input == 'price'){
                    this.orderPrice = currency;
                }else if(input == 'total_price'){
                    this.orderTotalPrice = currency;
                }else if(input == 'paid'){
                    this.orderPaid = currency;
                }else if(input == 'mato_adadi'){

                    this.matosList[index].amount = currency;
                }else if(input == 'yoqa_adadi'){
                    this.yoqasList[index].amount = currency;
                }else if(input == 'aksessuar_adadi'){
                    this.aksessuarList[index].amount = currency;
                }
            }
        },
        // ============= PRICE_FORMATTER END =============





        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            //-------------Lists---------------
            this.factorySelected = '',

            this.typeSelected = '',

            this.productSelected = '',
            //-------------Lists---------------

            this.orderAmount = '',
            this.orderPrice = '',
            this.orderTotalPrice = '',
            this.orderPaid = '',


            this.matosList = [{matoType: {}, mato: {}, amount: 0}],
            this.yoqasList = [{yoqaType: {}, yoqa: {}, amount: 0}],
            this.aksessuarList = [{aksessuarType: {}, aksessuar: {}, amount: 0}],

            this.matoIndex = '',
            this.yoqaIndex = '',
            this.aksessuarIndex = '',

            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        changeDialog1(){
            this.clearErrors();
            this.$refs.formSupplyOrder1.reset();
        },
        changeDialog2(){
            this.clearErrors();
            this.$refs.formSupplyOrder2.reset();
        },
        changeDialog3(){
            this.clearErrors();
            this.$refs.formSupplyOrder3.reset();
        },
        changeDialog4(){
            this.clearInputs();
            this.clearErrors();
            this.$refs.formSupplyOrder1.reset();
            this.$refs.formSupplyOrder2.reset();
            this.$refs.formSupplyOrder3.reset();
            this.$refs.formSupplyOrder4.reset();
            this.readyProduct = false;
        },
        // ============= CLEARS_AND_CLOSES END =============








        // ============= ON_SUBMITS START =============
        async onSubmit1(){
            this.$refs.formSupplyOrder1.validate().then(async(success) => {
                if(success){
                    this.changeDialog1();
                }
            })
        },
        async onSubmit2(){
            this.$refs.formSupplyOrder2.validate().then(async(success) => {
                if(success){
                    this.changeDialog2();
                }
            })
        },
        async onSubmit3(){
            this.$refs.formSupplyOrder3.validate().then(async(success) => {
                if(success){
                    this.changeDialog3();
                }
            })
        },
        async onSubmit4(){
            this.$refs.formSupplyOrder4.validate().then(async(success) => {
                 if(success){
                    // ============= IF SUCCESS =============
                    let order_extra_products = [];

                    this.matosList.forEach((elem) => {
                        if(elem.mato && elem.mato.id){
                            order_extra_products.push({
                                product_id: elem.mato.product_id,
                                amount: parseFloat(elem.amount.toString().replace(/,/g, '')),
                                product_case: "sewed",
                                color_code: elem.mato.color
                            })
                        }
                    })
                    this.yoqasList.forEach((elem) => {
                        if(elem.yoqa && elem.yoqa.id){
                            order_extra_products.push({
                                product_id: elem.yoqa.product_id,
                                amount: parseFloat(elem.amount.toString().replace(/,/g, '')),
                                product_case: "sewed",
                                color_code: elem.yoqa.color
                            })
                        }
                    })

                    this.aksessuarList.forEach((elem) => {
                        if(elem.aksessuar && elem.aksessuar.id){
                            order_extra_products.push({
                                product_id: elem.aksessuar.id,
                                amount: parseFloat(elem.amount.toString().replace(/,/g, '')),
                                product_case: "raw",
                                color_code: 'accessory'
                            })
                        }
                    })

                    // ============= ORDER START =============
                    let order = await this.postTailoringOrder(
                        {
                            factory_id: parseInt(this.factorySelected),
                            product_id: parseInt(this.productSelected.id),
                            type: 'sewing',
                            amount: parseFloat(this.orderAmount.toString().replace(/,/g, '')),
                            price: parseFloat(this.orderPrice.toString().replace(/,/g, '')),
                            paid: parseFloat(this.orderPaid.toString().replace(/,/g, '')),
                            total_price: parseFloat(this.orderTotalPrice.toString().replace(/,/g, '')),
                            created_at: this.date,
                            order_extra_products: order_extra_products
                        })

                    if(order.response && order.response.status == 422){
                        if(order.response.data.errors.amount){
                            this.errorMessage = order.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }else if(order.response.data.errors.price){
                            this.errorMessage = order.response.data.errors.price[0];
                            this.errorPlace = 'price'
                        }else if(order.response.data.errors.paid){
                            this.errorMessage = order.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }else if(order.response.data.errors.total_price){
                            this.errorMessage = order.response.data.errors.total_price[0];
                            this.errorPlace = 'total_price'
                        }
                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(order.response && order.response.status == 409){
                        if(order.response.data.message){
                            this.errorMessage = order.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        let toastTitle = 'Muvaffaqiyatli buyurtma berildi!'
                        this.$toast.success(toastTitle);
                        this.changeDialog4();
                        this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_MATERIAL_OUTCOME', true)
                        this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_MATERIAL_EXISTS', true)
                        this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_COLLAR_OUTCOME', true)
                        this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_COLLAR_EXISTS', true)
                        this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_OUTCOME', true)
                        this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_EXISTS', true)

                        this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_ORDER_HISTORY', true)
                        this.$store.commit('main/SET_UPDATE_FACTORY_CURRENT_PROCESS', true)

                    }
                    // ============= ORDER END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        },
        // ============= ON_SUBMITS END =============



    }

}
</script>



