<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="yoqaTypeSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma yoqa turini tanlash</span>
                        <v-btn icon @click="closeYoqaTypeSelect()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderCollarTypeSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit12)">
                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="yoqaTypeSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1">
                                <div class="radioContent mb-5" v-for="(element, index) in yoqaTypesList" :key="index">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.yoqasList[yoqaIndex].yoqaType">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeYoqaTypeSelect()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="yoqaSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma yoqani tanlash</span>
                        <v-btn icon @click="closeYoqaSelect()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderCollarSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit13)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="yoqaSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>
                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element, index2) in yoqas" :key="index2">
                                    <span>{{element.product.name}} - {{element.color}}</span>

                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }"
                                        class="d-flex align-center">
                                        
                                        <span class="mr-4 text-blue">{{formatPrice(element.amount)}} dona</span>

                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.yoqasList[yoqaIndex].yoqa">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeYoqaSelect()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { priceMixin } from '@/mixins/formatter.js'

export default {
    props: ['yoqaTypeSelectDialog', 'yoqaSelectDialog', 'yoqasList', 'yoqaIndex'],
    mixins: [dateMixin, loaderMixin, priceMixin],
    components: {
        PerfectScrollbar
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            pageYoqa: 1,
            pageCountYoqa: 0,
            last_pageYoqa: 2,

            yoqaTypesList: [],

            yoqaTypeSearch: '',
            yoqaSearch: '',

            yoqas: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh();
        await this.refresh2();
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        async yoqaTypeSearch(newValue, oldValue){
            if(newValue){
                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    if(newValue){
                        await this.fetchCategories({search: newValue});
                    }else{
                        await this.fetchCategories();
                    }
                    this.yoqaTypesList = this.productCategories.filter((elem) => elem.parent_id == 3);
                }, 500)
            }
        },


        async yoqaSearch(newValue, oldValue){
            this.pageYoqa = 1;
            if(newValue || newValue == ''){
                await this.refresh2();
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState(
            {
                productCategories: state => state.categories.productCategories,
                filtered_tailored_products: state => state.supply.filtered_tailored_products,
            }
        ),
    },
    methods: {
        ...mapActions(
            {
                fetchFilteredTailoredProducts: 'supply/fetchFilteredTailoredProducts',
                fetchCategories: 'categories/fetchCategories'
            }
        ),
        // ============= REFRESH START =============
        async refresh(){
            await this.fetchCategories()
            this.yoqaTypesList = this.productCategories.filter((elem) => elem.parent_id == 3)
        },
        // ============= REFRESH START =============





        // ============= REFRESH START =============
        async refresh2(){
            this.pageYoqa = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchFilteredTailoredProducts({product_case: 'sewed', 
                product_category_id: this.$parent.yoqasList[this.yoqaIndex].yoqaType.id,
                search: this.yoqaSearch})

                this.pageYoqa = this.filtered_tailored_products.current_page;
                this.last_pageYoqa = this.filtered_tailored_products.last_page;
                this.pageCountYoqa = Math.ceil(this.filtered_tailored_products.total / 10);
                
                this.yoqas = this.filtered_tailored_products.data;
            }, 500)
        },
        // ============= REFRESH END =============







        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.pageYoqa < this.last_pageYoqa){
                this.pageYoqa = this.pageYoqa + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                await this.fetchFilteredTailoredProducts({product_case: 'sewed', 
                product_category_id: this.$parent.yoqasList[this.yoqaIndex].yoqaType.id,
                search: this.matoSearch})

                this.pageYoqa = this.filtered_tailored_products.current_page;
                this.pageCountMato = Math.ceil(this.filtered_tailored_products.total / 10);
                    
                this.yoqas = this.filtered_tailored_products.data;
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============







        // ============= CLEARS_AND_CLOSES START =============
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeYoqaTypeSelect(){
            this.clearErrors()
            this.$refs.formOrderCollarTypeSelect.reset();
            this.$parent.yoqaTypeSelectDialog = false
        },
        closeYoqaSelect(){
            this.clearErrors()
            this.$refs.formOrderCollarSelect.reset();
            this.$parent.yoqaSelectDialog = false
        },
        // ============= CLEARS_AND_CLOSES END =============




        


        // ============= ON_SUBMIT12 START =============
        async onSubmit12(){
            this.$refs.formOrderCollarTypeSelect.validate().then(async(success) => {
                if(success){
                    this.closeYoqaTypeSelect()

                    await this.refresh2()
                }
            })
        },
        // ============= ON_SUBMIT12 END =============



        // ============= ON_SUBMIT13 START =============
        async onSubmit13(){
            this.$refs.formOrderCollarSelect.validate().then(async(success) => {
                if(success){
                    this.closeYoqaSelect()
                }
            })
        },
        // ============= ON_SUBMIT13 END =============

    }

}
</script>

