<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="matoTypeSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma mato turini tanlash</span>
                        <v-btn icon @click="closeMatoTypeSelect()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderMaterialTypeSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit10)">
                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="matoTypeSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>

                            <perfect-scrollbar class="mr-1">
                                <div class="radioContent mb-5" v-for="(element, index) in matoTypesList" :key="index">
                                    <span>{{element.name}}</span>
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }">
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.matosList[matoIndex].matoType">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeMatoTypeSelect()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>



        <v-dialog
            max-width="400"
            persistent
            v-model="matoSelectDialog"
        >
            <template>
                <v-card class="aksessuar_turi_add">
                    <div class="aksessuar_turi_add__title">
                        <span>Buyurtma matoni tanlash</span>
                        <v-btn icon @click="closeMatoSelect()">
                            <img src="@/assets/images/BackButton.svg" alt="">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formOrderMaterialSelect" v-slot="{ handleSubmit, invalid }">
                        <form class="aksessuar_turi_add__content" @submit.prevent="handleSubmit(onSubmit11)">

                            <div class="exists mt-5">
                                <span class="exists__title text-black">Bazadagi mahsulotlar</span>
                                <v-text-field outlined class="exists__input" v-model="matoSearch">
                                    <template v-slot:prepend-inner>
                                        <img src="@/assets/icons/search-normal.svg" class="mb-4 mt-0" alt="">
                                    </template>
                                </v-text-field>
                            </div>
                            <perfect-scrollbar class="mr-1" @ps-y-reach-end="addItems">
                                <div class="radioContent mb-5" v-for="(element, index2) in matos" :key="index2">
                                    <span>{{element.product.name}} - {{element.color}}</span>
                                    
                                    <ValidationProvider 
                                        rules="required"
                                        v-slot="{ errors }" class="d-flex align-center">
                                        <span class="mr-4 text-blue">{{formatPrice(element.amount)}} dona</span>
                                        <v-radio-group class="radioGroup" :error-messages="errors"
                                            v-model="$parent.matosList[matoIndex].mato">
                                            <v-radio :value="element"></v-radio>
                                        </v-radio-group>
                                    </ValidationProvider>
                                </div>
                            </perfect-scrollbar>

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    text
                                    class="backBtn"
                                    elevation="0"
                                    @click="closeMatoSelect()"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>
                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { priceMixin } from '@/mixins/formatter.js'

export default {
    props: ['matoTypeSelectDialog', 'matoSelectDialog', 'matosList', 'matoIndex'],
    mixins: [dateMixin, loaderMixin, priceMixin],
    components: {
        PerfectScrollbar
    },
    data() {
        return {
            // ============= SECONDARY_DATA =============
            pageMato: 1,
            last_pageMato: 2,
            pageCountMato: 0,

            matoTypesList: [],

            matoTypeSearch: '',
            matoSearch: '',

            matos: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    async mounted(){
        await this.refresh()
        await this.refresh2()
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        async matoTypeSearch(newValue, oldValue){
            if(newValue){
                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    if(newValue){
                        await this.fetchCategories({search: newValue});
                    }else{
                        await this.fetchCategories();
                    }
                    this.matoTypesList = this.productCategories.filter((elem) => elem.parent_id == 2);
                }, 500)
            }
        },


        async matoSearch(newValue, oldValue){
            this.pageMato = 1;
            if(newValue || newValue == ''){
                await this.refresh2();
            }
        },
        // ============= OTHER_WATCHERS END =============
    },
    computed: {
        ...mapState(
            {
                productCategories: state => state.categories.productCategories,
                filtered_tailored_products: state => state.supply.filtered_tailored_products,
            }
        ),
    },
    methods: {
        ...mapActions(
            {
                fetchFilteredTailoredProducts: 'supply/fetchFilteredTailoredProducts',
                fetchCategories: 'categories/fetchCategories'
            }
        ),
        // ============= REFRESH START =============
        async refresh(){
            await this.fetchCategories()
            this.matoTypesList = this.productCategories.filter((elem) => elem.parent_id == 2)
        },
        // ============= REFRESH START =============





        // ============= REFRESH START =============
        async refresh2(){
            this.pageMato = 1;
            
            clearTimeout(this._timerId)

            this._timerId = setTimeout(async () => {
                await this.fetchFilteredTailoredProducts({product_case: 'sewed',
                product_category_id: this.$parent.matosList[this.matoIndex].matoType.id,
                search: this.matoSearch})

                console.log(this.filtered_tailored_products)
                    
                this.pageMato = this.filtered_tailored_products.current_page;
                this.last_pageMato = this.filtered_tailored_products.last_page;
                this.pageCountMato = Math.ceil(this.filtered_tailored_products.total / 10);
                
                this.matos = this.filtered_tailored_products.data;
            }, 500)
        },
        // ============= REFRESH END =============







        // ============= ADD_ITEMS START =============
        async addItems(){
            if(this.pageMato < this.last_page){
                this.pageMato = this.pageMato + 1;

                clearTimeout(this._timerId)

                this._timerId = setTimeout(async () => {
                    await this.fetchFilteredTailoredProducts({product_case: 'sewed',
                    product_category_id: this.$parent.matosList[this.matoIndex].matoType.id,
                    search: this.matoSearch})

                    this.pageMato = this.filtered_tailored_products.current_page;
                    this.pageCountMato = Math.ceil(this.filtered_tailored_products.total / 10);
                    
                    this.matos = this.filtered_tailored_products.data;
                }, 500)
            }
        },
        // ============= ADD_ITEMS END =============






        // ============= CLEARS_AND_CLOSES START =============
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeMatoTypeSelect(){
            this.clearErrors()
            this.$refs.formOrderMaterialTypeSelect.reset();
            this.$parent.matoTypeSelectDialog = false
        },
        closeMatoSelect(){
            this.clearErrors()
            this.$refs.formOrderMaterialSelect.reset();
            this.$parent.matoSelectDialog = false
        },
        // ============= CLEARS_AND_CLOSES END =============




        


        // ============= ON_SUBMIT10 START =============
        async onSubmit10(){
            this.$refs.formOrderMaterialTypeSelect.validate().then(async(success) => {
                if(success){
                    this.closeMatoTypeSelect()

                    this.refresh2()
                }
            })
        },
        // ============= ON_SUBMIT10 END =============



        // ============= ON_SUBMIT11 START =============
        async onSubmit11(){
            this.$refs.formOrderMaterialSelect.validate().then(async(success) => {
                if(success){
                    this.closeMatoSelect()
                }
            })
        },
        // ============= ON_SUBMIT11 END =============

    }

}
</script>

