<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="readyProduct"
        >
            <template v-slot:activator="{ on, attrs }">

                <v-btn dark v-bind="attrs"
                       v-on="on" elevation="0" class="mr-2" style="background: #379c7c;">
                    <img src="@/assets/icons/convert-3d-cube.svg" alt="convert-3d-cube" class="mr-1" width="22">
                    <span class="fs-16px" style="text-transform: none;">Bichish</span>
                </v-btn>

            </template>

            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title ">
                    <span class="text-black">Mahsulot bichildi</span>
                    <v-btn icon @click="readyProduct = false">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <ValidationObserver ref="formProductTailored" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">

                        <div class="mt-3">
                            <span class="text-black ml-1">Sana</span>

                            <v-menu
                                v-model="dateMenu"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                transition="scale-transition"
                                offset-y
                                min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                        <v-text-field
                                            class="mt-2"
                                            placeholder="Tanlang"
                                            v-model="dateFormatted"
                                            readonly
                                            outlined
                                            v-bind="attrs"
                                            v-on="on"
                                            @blur="date = parseDate(dateFormatted)"
                                            :error-messages="errors"
                                        ></v-text-field>
                                    </ValidationProvider>
                                </template>
                                <ValidationProvider rules="required" v-slot="{ errors }">
                                <v-date-picker
                                    color="blue lighten-1"
                                    v-model="date"
                                    @input="dateMenu = false"
                                    locale="ru-ru"
                                    first-day-of-week="1"
                                    :error-messages="errors"
                                ></v-date-picker>
                                </ValidationProvider>
                            </v-menu>
                        </div>

                        <div>
                            <span class="text-black ml-1">Mahsulot turini</span>
                            <ValidationProvider :rules="{required: true}"
                             v-slot="{ errors }">
                                <v-select label="Tanlang" :items="typesList"
                                 item-text="name" item-value="id"
                                 v-model="product.type" class="mt-2" 
                                outlined :error-messages="errors"></v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Mahsulotni tanlang</span>
                            <ValidationProvider :rules="{required: true}"
                             v-slot="{ errors }">
                                <v-select label="Tanlang" v-model="product.name"
                                    class="mt-2" :items="productsList"
                                    item-text="name" item-value="product_id"
                                    outlined :error-messages="errors">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Rangni tanlang</span>
                            <ValidationProvider :rules="{required: true}"
                             v-slot="{ errors }">
                                <v-select label="Tanlang" v-model="product.color_code"
                                    class="mt-2" :items="productsColorsList"
                                    item-text="color" item-value="color"
                                    outlined :error-messages="errors">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Necha kg olindi</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'amount_using']}"
                             v-slot="{ errors }">
                                <v-text-field outlined class="mt-2" v-model="product.amount_using"
                                :suffix="availableMass + ' kg'" @input="formatPrice(product.amount_using, 'amount_using')"
                                onkeypress="return /[0-9,/.]/i.test(event.key)"
                                placeholder=" Misol: 400" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Necha dona bichildi</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'amount']}"
                             v-slot="{ errors }">
                                <v-text-field outlined
                                    v-model="product.amount" :error-messages="errors"
                                    @input="formatPrice(product.amount, 'amount')"
                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                    class="mt-2" placeholder="Misol: 30" suffix="dona">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Bichuv massasi</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'amount_sewed']}"
                             v-slot="{ errors }">
                                <v-text-field v-model="product.amount_sewed" 
                                    outlined :error-messages="errors" suffix="kg"
                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                    @input="formatPrice(product.amount_sewed, 'amount_sewed')"
                                    class="mt-2" placeholder="Misol: 380">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Isporeniya</span>
                            <!-- <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'from_where']}"
                             v-slot="{ errors }">
                                <v-text-field :error-messages="errors"
                                    outlined class="mt-2" v-model="product.evaporation"
                                    placeholder="200 kg">
                                </v-text-field>
                            </ValidationProvider> -->


                            <div class="d-flex">
                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'waste_amount']}"
                                v-slot="{ errors }" class="d-flex align-top w-75">
                                    <v-text-field :value="product.waste_amount" outlined class="mt-2" 
                                    readonly suffix="kg" :error-messages="errors"
                                    @input="formatPrice(product.waste_amount, 'waste_amount')">
                                    </v-text-field>
                                </ValidationProvider>
                                
                                <div class="d-flex align-top w-35">
                                    <v-text-field
                                        v-model="product.waste_percentage" 
                                        min="1" max="100" readonly
                                        outlined suffix="%" class="mt-2 ml-2">
                                    </v-text-field>
                                </div>
                            </div>
                        </div>

                        <div>
                            <span class="text-black ml-1">Summa</span>
                            <ValidationProvider :rules="{required: true, 
                            error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                             v-slot="{ errors }">
                                <v-text-field :error-messages="errors"
                                    @input="formatPrice(product.total_price, 'total_price')"
                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                    outlined class="mt-2" v-model="product.total_price"
                                    placeholder="Misol: 240,000 so‘m">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Berildi</span>
                            <ValidationProvider 
                            :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}"
                             v-slot="{ errors }">
                                <v-text-field :error-messages="errors"
                                    outlined v-model="product.paid"
                                    onkeypress="return /[0-9,/.]/i.test(event.key)"
                                    class="mt-2" @input="formatPrice(product.paid, 'paid')"
                                    placeholder="Misol: 140,000 so’m">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tasdiqlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js';

export default {
    mixins: [dateMixin, loaderMixin],
    data() {
        return {
            // ============= SECONDARY_DATA =============
            readyProduct: false,

            typesList: [
                { name: 'Mato', id: 2 },
                { name: 'Yoqa', id: 3 } 
            ],
            productPage: 1,
            productPageCount: 0,
            productsList: [],
            productsColorsList: [],
            availableMass: '',
            product: {
                type: '',
                name: '',
                color_code: '',
                amount_using: '',
                amount: 0,
                amount_sewed: '',
                waste_amount: '',
                waste_percentage: '',
                total_price: '',
                paid: 0
            },
            // ============= SECONDARY_DATA =============


            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: '',
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= NEW_IP START =============
        'product': {
            async handler(newVal){
                if(newVal){
                    this.clearErrors()
                }   
            }, deep: true,
        },
        'product.type': {
            async handler(newVal){
                if(newVal){
                    await this.fetchStoragePaintedProduct({page: this.page, product_case: 'painted', parent_id: newVal});

                    this.productPage = this.storage_painted_product.current_page;
                    this.productPageCount = Math.ceil(this.storage_painted_product.total / 20);

                    this.productsList = this.storage_painted_product.data;

                    this.productsList.forEach((elem) => {
                        elem.code = elem.product.code
                        elem.name = elem.product.name
                    })
                }   
            }, deep: true,
        },
        'product.name': {
            async handler(newVal){
                if(newVal){
                    this.productsColorsList = this.productsList.filter((elem) => {
                        return elem.product_id == newVal
                    })
                }   
            }, deep: true,
        },
        'product.color_code': {
            async handler(newVal){
                if(newVal){
                    let product = this.storage_painted_product.data.filter((elem) => {
                        return elem.color == newVal
                    })
                    this.availableMass = product[0].amount
                }   
            }, deep: true,
        },
        'product.amount_using': {
            async handler(newVal){
                if(newVal > this.availableMass){
                    this.errorMessage = 'Kiritilgan kilogram miqdori mavjud miqdordan oshib ketdi!'
                    this.errorPlace = 'amount_using';

                    this.errorStatus = true;
                    this.$toast.error(this.errorMessage);
                }else{
                    this.calculateEvaporation()
                }
            }, deep: true,
        },

        'product.amount_sewed': {
            async handler(newVal){
                if(newVal){
                    this.calculateEvaporation()
                }
            }, deep: true,
        },
        // ============= NEW_IP END =============



        // ============= OTHER_WATCHERS START =============
    
        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){

    },
    computed: {
        ...mapState(
            {
                product_tailored: state => state.supply.product_tailored,
                storage_painted_product: state => state.supply.storage_painted_product
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                postProductTailored: 'supply/postProductTailored',
                fetchStoragePaintedProduct: 'supply/fetchStoragePaintedProduct'
            }
        ),


        // ============= CALCULATIONS START =============
        calculateEvaporation(){
            this.product.waste_amount = parseFloat(this.product.amount_using.toString().replace(/,/g, ''))
             - parseFloat(this.product.amount_sewed.toString().replace(/,/g, ''))
                    
            let a = this.product.amount_sewed.toString().replace(/,/g, '') * 100
            this.product.waste_percentage = a / parseFloat(this.product.amount_using.toString().replace(/,/g, ''))
            this.product.waste_percentage =  (100 - this.product.waste_percentage).toFixed(3);
        },
        // ============= CALCULATIONS START =============





        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){
                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'amount_using'){
                    this.product.amount_using = currency
                }else if(input == 'amount_sewed'){
                    this.product.amount_sewed = currency
                }else if(input == 'waste_amount'){
                    this.product.waste_amount = currency
                }else if(input == 'total_price'){
                    this.product.total_price = currency
                }else if(input == 'paid'){
                    this.product.paid = currency
                }
            }
        },
        // ============= PRICE_FORMATTER END =============








        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.productsList = [];
            this.availableMass = '';
            this.product = {
                type: '',
                name: '',
                amount_using: '',
                amount: 0,
                amount_sewed: '',
                waste_amount: '',
                waste_percentage: '',
                total_price: '',
                paid: 0
            },
            this.date = '';
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeDialog(){
            this.clearInputs();
            this.readyProduct = false;
            this.$refs.formProductTailored.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============












        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formProductTailored.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    // ============= ADDING_NEW_IP START =============
                    if(this.errorStatus == false){
                        let abc = await this.postProductTailored(
                            { 
                                product_id: parseInt(this.product.name),
                                color_code: this.product.color_code, 
                                amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                                amount_using: parseFloat(this.product.amount_using.toString().replace(/,/g, '')),
                                amount_sewed: parseFloat(this.product.amount_sewed.toString().replace(/,/g, '')),
                                waste_percentage: parseFloat(this.product.waste_percentage.toString().replace(/,/g, '')),
                                waste_amount: parseFloat(this.product.waste_amount.toString().replace(/,/g, '')),
                                total_price: parseFloat(this.product.total_price.toString().replace(/,/g, '')),
                                paid: parseFloat(this.product.paid.toString().replace(/,/g, '')),
                                created_at: this.date,
                            })

                        if(parseFloat(this.product.amount_sewed.toString().replace(/,/g, '')) 
                        > parseFloat(this.product.amount_using.toString().replace(/,/g, ''))){
                            this.errorMessage = 'Kiritilgan bichuv massasi olingan massadan oshib ketdi!'
                            this.errorPlace = 'tailoring_mass';

                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }
    
                        if(abc.response && abc.response.status == 422){
                            if(abc.response.data.errors.amount){
                                this.errorMessage = abc.response.data.errors.amount[0];
                                this.errorPlace = 'amount'
                            }else if(abc.response.data.errors.amount_using){
                                this.errorMessage = abc.response.data.errors.amount_using[0];
                                this.errorPlace = 'amount_using'
                            }else if(abc.response.data.errors.amount_sewed){
                                this.errorMessage = abc.response.data.errors.amount_sewed[0];
                                this.errorPlace = 'amount_sewed'
                            }else if(abc.response.data.errors.waste_amount){
                                this.errorMessage = abc.response.data.errors.waste_amount[0];
                                this.errorPlace = 'waste_amount'
                            }else if(abc.response.data.errors.total_price){
                                this.errorMessage = abc.response.data.errors.total_price[0];
                                this.errorPlace = 'total_price'
                            }else if(abc.response.data.errors.paid){
                                this.errorMessage = abc.response.data.errors.paid[0];
                                this.errorPlace = 'paid'
                            }

                            this.errorStatus = true
                            this.$toast.error(this.errorMessage);
                        }else if(abc.response && abc.response.status == 409){
                            if(abc.response.data.message){
                                this.errorMessage = abc.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            let toastTitle = 'Muvaffaqiyatli qo`shildi!'
                            this.$toast.success(toastTitle);
                            this.closeDialog();
                            this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_HISTORY', true)
                            this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_WAREHOUSE', true)
                        }
                    }
                    // ============= ADDING_NEW_IP END =============

                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============
    }
}
</script>


<style>

</style>
